body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;font-weight:500;background:"#fff"}*{margin:0;padding:0;box-sizing:border-box}ul,li{margin:0;padding:0;list-style:none}a{text-decoration:none}.nav-container{width:280px;height:100vh;overflow:auto;padding:12px;display:flex;flex-direction:column;color:#444;border-right:1px solid #f3f3f3}.nav-container .logo img{width:46px;height:46px;border-radius:50%;object-fit:cover;border-radius:50%;background:#999;flex-shrink:0}.nav-container .menu-container{position:relative;display:flex;flex-direction:column;justify-content:space-between;height:100%}.nav-container .menu-container .menu-list .btn{width:100%;height:48px;font-weight:bold;margin-top:10px;display:flex;align-items:center;justify-content:center;background-color:#1da1f2}.nav-container .menu-container .menu-list .btn:hover{background-color:rgba(29,161,242,0.7)}.nav-container .menu-container .menu-list ul li{display:flex;padding:4px 0;height:60px}.nav-container .menu-container .menu-list ul li .menu-item{padding:12px;color:#0f1419;display:flex;align-items:center;font-weight:bold;font-size:18px;cursor:default}.nav-container .menu-container .menu-list ul li .menu-item .icon{font-size:24px;margin-right:10px}.nav-container .menu-container .menu-list ul li .menu-item.active{color:#1da1f2}.nav-container .menu-container .menu-list ul li .menu-item:hover{background:rgba(29,161,242,0.1);border-radius:24px}.nav-container .menu-container .menu-list ul li .menu-item:hover .icon{color:#1da1f2}.nav-container .user-name{padding-left:10px}.nav-container .avatar{display:flex;align-items:center}@media screen and (max-width: 800px){.nav-container{width:66px;padding:4px;display:flex;align-items:center}.nav-container .menu-container .menu-list .btn{width:48px;height:48px;border-radius:50%;margin:0 auto;font-size:22px}.nav-container .menu-container .menu-list ul li{padding:0;display:flex;align-items:center;justify-content:center}.nav-container .menu-container .menu-list ul li .menu-item{padding:12px}.nav-container .menu-container .menu-list ul li .menu-item .icon{margin-right:0}.nav-container .menu-container .menu-list ul li .menu-item .text{display:none}.nav-container .menu-container .menu-list ul li .menu-item:hover{background:rgba(29,161,242,0.1)}.nav-container .menu-container .menu-list ul li .menu-item:hover .icon{color:#1da1f2}.nav-container .user-name{padding-left:10px;display:none}.nav-container .avatar{justify-content:center}}
