body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;font-weight:500;background:"#fff"}*{margin:0;padding:0;box-sizing:border-box}ul,li{margin:0;padding:0;list-style:none}a{text-decoration:none}.app-container{display:grid;grid-template-columns:1.1fr 2fr}.app-container .header{display:flex;justify-content:flex-end}@media screen and (max-width: 500px){.app-container{grid-template-columns:66px auto}}
